* {
  box-sizing: border-box;
  font-family: 'ZCOOL XiaoWei', serif;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #CDDDDC;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.login {
  margin-top: 110px;
  position: relative;
  background-color: #FEECC8;
  border: 2px solid #6D3E48;
  border-radius: 1rem;
  box-shadow: 0px 1px 3px 2px #0000005c;
}

form {
  height: 24rem;
  width: 20rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}

main {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 44vw;
  height: 33rem;
}

.button-holder {
  display: flex;
}

.email-input,
.password-input,
.business-name-input,
.address-input,
.phone-number-input,
.contact-name-input,
.food-type-input {
  background-color: #fef9ee;
  border: 1px solid #6D3E48;
  display: flex;
  width: 19rem;
  align-content: center;
  height: 2rem;
  margin-top: 1rem;
  border-radius: 5px;
  padding-left: 5px;
  font-size: 1.3rem;
  opacity: 1;
}

.signin-button, .signup-button {
  display:flex;
  align-items: center;
  justify-content: center;
  color: white;
  background-color: #6D3E48;
  width: 19rem;
  height: 2rem;
  border: none;
  border-radius: 5px;
  font-size: 1.1rem;
  margin: 5px;
}

.email-input::-webkit-input-placeholder,
.name-input::-webkit-input-placeholder,
.password-input::-webkit-input-placeholder {
  color: #8C8C8C;
}

.email-input::-ms-input-placeholder,
.name-input::-ms-input-placeholder,
.password-input::-ms-input-placeholder {
  color: #8C8C8C;
}

.email-input::placeholder,
.name-input::placeholder,
.password-input::placeholder {
  color: #8C8C8C;
}

.business-type, .brewery-label, .food-truck-label {
  font-size: 1.3rem;
}

.business-type {
  margin-top: 8px;
  padding: 5px;
}

.logo-input {
  margin-top: 1.3rem;
}

.logo-button {
  width: 2rem;
}

.hidden {
  display: none;
  -webkit-transition: all .2s;
  transition: all .2s;
}
.business-holder {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 720px;

}

.business-header {
    font-size: 4rem;
    font-family: "Averia Libre", cursive;
    text-align: center;
    color: #6d3e48;
    margin: 5px;
}

.business-button {
    font-family: 'ZCOOL XiaoWei', serif;
    color: black;
    font-size: 1.2rem

}
.main {
  background-color: #cddddc;
  display: grid;
  grid-template-rows: 130px 1fr;
  height: 100vh;
}
header {
  background-color: #feecc8;
  padding: 1rem;
  grid-row: 1 / span 1;
}

main {
  grid-row: 2 / span 1;
}

#wrapper {
    width: 240px;
    padding-left: 0.5rem;
    height: 100vh;
    position: absolute;
    left:0px;
    top: 0px;
    -webkit-transition: all 0.7s;
    transition: all 0.7s;
}

.main-title {
  font-family: "Averia Libre", cursive;
  text-align: center;
  color: #6d3e48;
  font-size: 4.5rem;
  letter-spacing: 0.2em;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
section {
  position: relative;
  bottom: 70px;
  height: 200px;
  box-sizing: border-box;
}

section:after {
  position: absolute;
  top: 1.5em;
  left: 1.5em;
  width: 40px;
  height: 40px;
  border: 1px solid #6D3E48;
  color: #6D3E48;
  font-size: 20px;
  text-align: center;
  line-height: 20px;
}
.hamburger-trigger,
.hamburger-trigger span {
  display: inline-block;
  -webkit-transition: all .2s;
  transition: all .2s;
  box-sizing: border-box;
  z-index: 5;
}
.hamburger-trigger {
  position: relative;
  width: 50px;
  height: 44px;
}
.hamburger-trigger.true span {
  border: 2px solid #FEECC8;
  background-color: #FEECC8;
  -webkit-transition: all .2s;
  transition: all .2s;
}
.hamburger-trigger span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 5px;
  background-color: #6D3E48;
  border-radius: 6px;
}
.hamburger-trigger span:nth-of-type(1) {
  top: 0;
}
.hamburger-trigger span:nth-of-type(2) {
  top: 20px;
}
.hamburger-trigger span:nth-of-type(3) {
  bottom: 0;
}
#hamburger.true span:nth-of-type(1) {
  -webkit-transform: translateY(20px) rotate(-45deg);
  transform: translateY(20px) rotate(-45deg);
}
#hamburger.true span:nth-of-type(2) {
  left: 50%;
  opacity: 0;
  -webkit-animation: active-hamburger-bar02 .8s forwards;
  animation: active-hamburger-bar02 .8s forwards;
}
#hamburger.true span:nth-of-type(3) {
  -webkit-transform: translateY(-20px) rotate(45deg);
  transform: translateY(-20px) rotate(45deg);
}

.truck-icon,
.barrel-icon {
  height: 65px;
  padding-left: 10px;
  padding-right: 30px;
}
.register {
  font-size: 1.2rem;
}
.content-holder {
  display: flex;
  justify-content: center;
}

.logout-button {
  position: absolute;
  right: 40px;
  top: 30px;
  background-color: #6D3E48;
  color: #fff;
  height: 35px;
  font-size: 15px;
}
.parent-container {
  font-family: 'ZCOOL XiaoWei', serif;
  width: 100%;
}

.profile-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}

.biz-name {
  font-family: 'Averia Libre', cursive;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-decoration: underline;
  color: #6D3E48;
  font-size: 4rem;
  margin-top: 3rem;
  margin-bottom: 5rem;
}

.biz-title { 
  font-size: 1.6rem;
  font-weight: 800;
  margin-bottom: 0.6rem;
}

.biz-info {
  font-size: 1.4rem;
  margin-bottom: 1.7rem;
}


.biz-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 45rem;
}

.upcoming-events-container {
  width: 32rem;
  margin-top: 12rem;
}

.upcoming-events-title {
  background-color: #8a646c;
  font-size: 2rem;
  line-height: 5rem;
  font-weight: 400;  
  text-align: center;
  border: 2px solid; 
}

.upcoming-events-date {
  display: flex;
  justify-content: space-between;
  font-size: 1.5rem;
  line-height: 3.5rem;
  margin: 0;
  padding-left: 1.2rem;
  background-color: #FEECC8;
  border: 2px solid;
  color: #594E42;
}

.booked {
  border: 2px solid green;
}

.open {
  border: 2px solid red;
}

.upcoming-events-status {
  margin-left: 2rem;
}

.calendar {
  display: inline-block;
  width: 10rem;
  margin-bottom: 3rem;
}

.day {
  font-size: 1.4rem;
  margin-bottom: 1rem;
  font-weight: 800;
}
.circle {
  padding-top: 40px;
  height: 90px;
  width: 90px;
  background-color: #6D3E48;
  border-radius: 50%;
  text-align: center;
  color: #FEECC8;
}

