    position: absolute;
    left: 72%;
    top: 210px;
    width: 180px;
    height: 52px;
    font-size: 1.5rem;
    border-radius: 13px;
    background-color: #FEECC8;
    border: 2px solid #8a646c;
}

.create-event-form {
  display: flex;
  flex-direction:column;
  align-items: center;
  justify-content: space-around;
  position: absolute;
  left: 41%;
  top: 34%;
  border-radius: 50px;
  background-color: #FEECC8;
  border: 2px solid #8a646c;
  box-shadow: 0 0 820px 820px #00000070;
}

.create-event-date {
  display: inline-block;
  font-size: 40px;
}

.create-event-input {
  width: 170px;
  height: 55px;
  font-size: 30px;
  margin-bottom: 0;
  display: inline-block;
}

.create-event-input::-webkit-input-placeholder {
  font-size: 30px;
}

.create-event-input::-ms-input-placeholder {
  font-size: 30px;
}

.create-event-input::placeholder {
  font-size: 30px;
}

.create-event-form-button {
  width: 110px;
  height: 45px;
  background-color: #6D3E48;
  color: #fff;
  font-weight: 700;
  font-size: 25px;
}

.change-status-button {
  background-color: #6D3E48;
  color: #fff;
  margin-left: 3.5rem;
  height: 35px;
  font-size: 15px;
}

.delete-status-button {
  background-color: #6D3E48;
  color: #fff;
  margin-left: 2rem;
  height: 35px;
  font-size: 15px;
  margin-right: 5px;
}
nav {
    width: 240px;
    padding-left: 0.5rem;
    background-color: #6D3E48;
    height: 100vh;
    position: absolute;
    left:0px;
    top: 0px;
    -webkit-transition: all 0.7s;
    transition: all 0.7s;
}

.nav-controls {
    margin-top: 200px;
    height: 350px;
    width: 102%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
    font-family: 'ZCOOL XiaoWei', serif;
    padding: 0;
}

.nav-control, .search-input {
    font-size: 1.7rem;
    padding: 8px;
    width: 100%;
    height: 16%;
    margin: 0;
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #CDDDDC
}


.search-input {
    border:none;
    width: 100%;
    padding: 0px;
}

.nav-control:hover {
    background-color: #CDDDDC;
    color: #6D3E48;
    margin: 0;
}

.hidden-nav {
   width: 0px;
   border: none;
   margin: 0px;
   padding: 0px;
   -webkit-transition: all .4s;
   transition: all .4s;
}



